body,html{height:100%;width:100%;font-family:Montserrat,'Helvetica Neue',Arial,sans-serif;font-weight:100;color:#fff;overflow:hidden}a,h1,i,li,ul{-webkit-transition:.3s ease-in-out;transition:.3s ease-in-out}section{-webkit-transition:.7s ease-in-out;transition:.7s ease-in-out}a{color:inherit!important}li,ul{list-style-type:none!important;margin:0;padding:0}a,button,div,i,select{text-decoration:none!important;outline:0!important}section{position:absolute;height:100%;width:100%;color:#fff;text-shadow:.1em .1em .3em rgba(0,0,0,.7)}.bg-image{background-attachment:fixed!important;background-size:cover!important;background-repeat:no-repeat!important;background-position:center center!important;position:fixed;height:100%;width:100%;z-index:1;-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:.7s ease-in-out;transition:.7s ease-in-out;opacity:0}.show-image{opacity:1}.links>ul>li:hover{-webkit-transform:translateX(.2em);transform:translateX(.2em)}.links{position:fixed;top:0;left:0;padding:.5em .3em;height:100%;background:-webkit-linear-gradient(left,rgba(0,0,0,.6),rgba(0,0,0,.5),rgba(0,0,0,.4),rgba(0,0,0,.3),transparent);background:linear-gradient(to right,rgba(0,0,0,.6),rgba(0,0,0,.5),rgba(0,0,0,.4),rgba(0,0,0,.3),transparent);-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-flow:column;flex-flow:column;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-transition:1s cubic-bezier(.83,-.03,.54,.99);transition:1s cubic-bezier(.83,-.03,.54,.99);opacity:0;-webkit-transform:translate(-100%,0);transform:translate(-100%,0)}.links>ul>li>a:hover{color:rgba(255,255,255,.4)!important}.links>ul{overflow-y:scroll;overflow-x:hidden}.about::-webkit-scrollbar,ul::-webkit-scrollbar{display:none}.links>ul>li{padding-right:.5em}.footer{left:0;bottom:0;font-size:.2em;padding:.5em;width:100%;text-align:center;z-index:-1;margin-top:auto}.copyright,.tag{font-size:1.4em}.profile{background:url(public/images/profile.jpg);height:10em;width:10em;box-shadow:0 0 1em 0 #000;background-size:cover;border-radius:5em;background-position:center center;margin:1.5em auto;-webkit-transition:.3s ease-in-out;transition:.3s ease-in-out;cursor:pointer}.profile:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.main{z-index:3}@media (max-width:768px){.bg-image{background:url(http://i.imgur.com/R1KRe85h.jpg)}.links{font-size:2em}}@media (min-width:769px){.links{font-size:3em}}.about{-webkit-transition:1s cubic-bezier(.83,-.03,.54,.99);transition:1s cubic-bezier(.83,-.03,.54,.99);opacity:0;position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%,-150%);transform:translate(-50%,-150%);z-index:4;text-shadow:.1em .1em .3em rgba(0,0,0,.7);font-size:2em;padding:.5em .2em;text-align:center;border-radius:.2em;overflow-y:scroll;height:90%;width:90%;background:-webkit-linear-gradient(rgba(0,0,0,.3),rgba(0,0,0,.3),rgba(0,0,0,.3),transparent);background:linear-gradient(rgba(0,0,0,.3),rgba(0,0,0,.3),rgba(0,0,0,.3),transparent)}.about>.about-close-btn{font-size:1em;position:absolute;right:0;top:0;padding:.1em .3em;cursor:pointer}.about>span{display:block}.about>div>.fa-user{font-size:2em}.about-text{margin:.3em 0}.about-visible{opacity:1;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}.links-visible{opacity:1;-webkit-transform:translate(0,0);transform:translate(0,0)}.contact-links>a>i:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.loader{font-size:2em;opacity:0;position:fixed;top:50%;left:50%;color:#333;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);-webkit-transition:.3s ease-in-out;transition:.3s ease-in-out}.loader-visible{opacity:1}
